@charset 'UTF-8';
*,
*:before,
*:after {
  box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
}
body {
  font-family: 'Roboto', sans-serif;
  color: #17324d;
  font-size: 100%;
  text-rendering: optimizeLegibility;
  -webkit-overflow-scrolling: touch;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  user-select: text;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  margin: 0px;
  line-height: 1;
  background-color: #fbfbfb;
}
.strong {
  font-weight: 700;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  font-size: 100%;
  vertical-align: baseline;
}
ol,
ul {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  line-height: 1.25;
}
h1 {
  font-size: 1.5rem;
  color: #0a1f33;
}
h2 {
  font-size: 1.25rem;
  color: #0a1f33;
}
h3 {
  font-size: 1.125rem;
}
h4 {
  font-size: 1rem;
}
h5 {
  font-size: 0.875rem;
}
h6 {
  font-size: 0.75rem;
}
a {
  color: #5c7a99;
  text-decoration: none;
  transition: 0.1s color linear;
}
a:hover {
  color: #005fb2;
}
a:active {
  color: #00396b;
}
.svgwrapper .icon {
  display: inline-block;
}
svg {
  pointer-events: none;
}
iframe {
  border: none;
}
#content {
  max-width: 1500px;
  width: 90%;
  display: flex;
  flex-direction: column;
  min-height: 85vh;
  justify-content: space-between;
}
#content > div {
  display: flex;
  flex: auto;
  flex-direction: column;
}
#root {
  margin-top: 85px;
  display: flex;
  justify-content: center;
}
#root.no-margin-left {
  margin-left: 0;
}
body .header {
  top: 0;
  left: auto;
  right: 0;
  position: fixed;
  width: 100%;
  z-index: 1500;
}
.toolBar {
  display: flex;
  justify-content: space-between;
  background-color: #2fa1b5;
}
.toolBar .headerButtonContainer {
  padding: 8px;
  margin: 2px;
}
.toolBar .headerButtonContainer .headerButton {
  color: #fff;
}
.headerTitle {
  padding: 0 10px;
  border-left: 2px solid #fff;
}
.left,
.right {
  display: flex;
  align-items: center;
}
.navBarContainer {
  z-index: 600;
  position: fixed;
  top: 0;
}
.navBarContainer .navBar {
  margin-top: 85px;
}
.navBarContainer .navBar .navBarItem {
  color: #5c7a99;
}
.navBarContainer .navBar .navBarItem .navBarLabel {
  padding: 0 12px 0 0;
}
.navBarContainer .navBar .navBarSubList {
  padding: 0;
}
.areaSeries {
  margin: 30px;
}
.lineSeries {
  margin: 30px;
}
.sampleCode {
  background-color: #fff;
  width: 100%;
  font-size: 1em;
}
.colorsDisplay iframe {
  width: 100%;
  height: 100vh;
}
.avatarContainer {
  margin: 10px;
  width: 100%;
}
.avatarContainer .avatarContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #26a69a;
}
.avatarContainer .avatarContent .avatarGrid {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  margin: 10px;
  width: 80%;
  padding: 10px;
  background-color: #eee;
}
.avatarContainer .avatarContent .avatarGrid .pikachu {
  background-color: #ff0;
}
.avatarContainer .avatarContent .avatarGrid .letterS {
  background-color: #ef5350;
}
.avatarContainer .avatarContent .avatarGrid .letterLA {
  background-color: #42a5f5;
}
.avatarContainer .avatarContent .avatarGrid .pageview {
  background-color: #b9f6ca;
  color: #ff8a65;
}
.avatarContainer .avatarContent .avatarGrid .assignment {
  background-color: #ba68c8;
}
.buttonContainer {
  margin: 10px;
  width: 100%;
}
.buttonContainer .buttonContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #26a69a;
}
.buttonContainer .buttonContent .buttonGrid {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  margin: 10px;
  width: 80%;
  padding: 10px;
  background-color: #eee;
}
.chipContainer {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.chipContainer .chip {
  margin: 10px;
}
.sidebar {
  width: 100vw;
  transition: margin-left 225ms;
  justify-content: center;
}
.open {
  margin-left: 250px;
}
.close {
  margin-left: 0;
}
.mainContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 5px;
}
.mainContainer .tabContainer {
  background-color: #6fafb5;
  color: #000;
  display: flex;
  justify-content: space-between;
}
